
import { defineComponent } from 'vue'
import { APM20400111_Req_Body, APM20400111_Res_List } from '@/shared/type/apm204001sc00.type';
import pagination from "@/shared/components/Pagination/Pagination.vue";
import { PAGE_SIZE } from '@/shared/constants/common.const';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';

export default defineComponent({
    name: "apm204001sc00",
    components: {
        pagination,
        BreadCrumb
    },
    data() {
        const headers = [
            { columnName: "No." },
            { columnName: "View" },
            { columnName: "Customer No" },
            { columnName: "Customer Name" },
            { columnName: "Master User ID" },
            { columnName: "Master User Name" },
            { columnName: "Authorization Line Setup Count" },
            { columnName: "Authorization Line Option" },
            { columnName: "Authorization Line Policy Code" },
            { columnName: "Authorization Line Role / Person" }
        ];
        return {
            headers,
            dataTable: [] as APM20400111_Res_List[],
            totalCount: 0 as number,
            startingIndex: 1 as number,
            isResetPage: false as boolean,
            search: {
                customerNo: "",
                customerName: "",
                authorizationLinePolicyTypeCode: "",
                pageNumber: 1,
                pageSize: PAGE_SIZE,
            } as APM20400111_Req_Body,
            myObj: {
                LimitTypeCode: [],
                LanguageCode: [],
                StatusCode: []
            },
        }
    },
    created() {
        this.requestData(false);
    },
    computed:{
        authorizationLinePolicyTypeCode(){
            let authorizationLinePolicyItems = [] as any;
            this.$store.state.CommonCodeModule.commonCode.AuthorizationLinePolicyTypeCode.filter((item: any)=> {
                if(item.key != '03'){
                    authorizationLinePolicyItems.push(item);
                }
            })
            return authorizationLinePolicyItems;
        }
    },

    methods: {
        async requestData(isSearch: boolean) {
            const body: APM20400111_Req_Body = {
                authorizationLinePolicyTypeCode: this.search.authorizationLinePolicyTypeCode,
                customerName: this.search.customerName,
                customerNo: this.search.customerNo,
                pageNumber: isSearch ? this.search.pageNumber = 1 : this.search.pageNumber,
                pageSize: this.search.pageSize,
            }
            if(isSearch){
                this.isResetPage = true;
            }
            await this.$request.getRequest('APM20400111', body, false)
                .then((res: any) => {
                    if (res.header.result) {
                        this.totalCount = res.body.totalCount;
                        let list: any;
                        list = res.body.list;
                        this.dataTable = list.map((item: any) => ({
                            authLineCount: item.authLineCount,
                            authorizationLineAssignTypeCode: item.authorizationLineAssignTypeCode,
                            authorizationLinePolicyTypeCode: item.authorizationLinePolicyTypeCode,
                            customerName: item.customerName,
                            customerNo: item.customerNo,
                            masterUserID: item.masterUserID,
                            masterUserName: item.userName,
                            subscriptionApprovalTypeCode: item.subscriptionApprovalTypeCode
                        }))
                        this.totalCount = res.body.totalCount;
                    }
                })
        },

        refreshSearch() {
            this.search.pageSize = PAGE_SIZE;
            this.search.customerName = "";
            this.search.customerNo = "";
            this.search.authorizationLinePolicyTypeCode = "";
            this.search.pageNumber = 1;
            this.isResetPage = true;
            this.requestData(true);
        },

        handleSearch() {
            if (this.search.customerName == '' && this.search.customerNo == '' && this.search.authorizationLinePolicyTypeCode == '') {
                this.isResetPage = true;
                this.search.pageNumber = 1;
                this.search.pageSize = PAGE_SIZE;
                this.requestData(true);
            } else {
                this.requestData(true);
            }


        },

        handleClickDetail(data: APM20400111_Res_List) {
            this.$router.push({
                path: 'APM204001SC01',
                query: { customerNo: data.customerNo, masterUserID: data.masterUserID }
            })
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false
            this.search.pageNumber = pageNumber;
            this.requestData(false);
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },
    }
})
